import React from 'react'
import { LegalSection, Page, Seo } from 'gatsby-theme-portfolio-minimal';

export default function CocoaCloneCoding() {
    return (
        <>
            <Seo title="CocoaCloneCoding" useTitleTemplate={true} noIndex={true}  />
            <Page useSplashScreenAnimation>
            <LegalSection sectionId="CocoaCloneCoding" heading="CloneCoding" />
            </Page>
        </>
    );
}